<template>
    <el-cascader :props="config"
                 class="region"
                 v-model="regions"
                 :separator="separator"
                 :placeholder="placeholder"
                 :size="size">
    </el-cascader>
</template>

<script>
    export default {
        name: "regionView",
        props: {
            //大小
            size: {
                type: String,
                default: 'small'
            },
            //分割线
            separator:{
                type: String,
                default: ' - '
            },
            //默认值，但是无法同步到model，因为是动态加载的，哎
            value: {
                type: Array,
                default: ()=>{
                    return [];
                }
            },
            //提示文字
            placeholder: {
                type: String,
                default: ''
            }
        },
        model:{
            prop: 'value',
            event: 'change',
        },
        computed:{
            regions:{
                get(){
                    return this.value;
                },
                set(val){
                    this.$emit('change',val);
                }
            }
        },
        data() {
            const _self  = this;
            return {
                config: {
                    lazy: true,
                    //node是当前节点，resolve是返回值
                    lazyLoad(node, resolve) {
                        let pid = 0;
                        if(node){
                            pid = node.value;
                        }
                        _self.getRegion(pid,(data)=>{
                            let re = [];
                            for (let item in data){
                                if(data[item]){
                                    re.push({
                                        value: data[item].id,
                                        label: _self.$lang() === 'lang_0' || _self.$lang() === 'lang_1' ? data[item].name : data[item].name_en,
                                    });
                                }
                            }
                            resolve(re);
                        },(error)=>{
                            console.log(error);
                            resolve([]);
                        });
                    }
                }
            };
        },
        methods: {
            getRegion(pid,successFunc, errorFunc) {
                const _self = this;
                _self.$api('getRegion', {pid: pid}, function (reData) {
                    if (reData.flag === 1) {
                        if (typeof successFunc === "function") {
                            successFunc(reData.data);
                        }
                    } else {
                        //未知错误
                        if (typeof errorFunc === "function") {
                            errorFunc(reData);
                        }
                    }
                }, function (error) {
                    //网络请求错误
                    if (typeof errorFunc === "function") {
                        errorFunc(error);
                    }
                });
            }
        }
    }
</script>

<style lang="scss">
    .el-cascader-menu{
        overflow-x: hidden!important;
        .el-scrollbar__wrap{
            overflow-x: hidden!important;
        }
    }
</style>